import './Style/App.css';
import React from 'react';
import Series from './Series';
import Header from './Header'
import Tmdb from './Tmdb'
import GenresSeries from './GenresSeries';
import GenresFilmes from './GenresFilmes';
import { useEffect } from 'react';
import { useState } from 'react';
import BibliotecaSerie from './BibliotecaSerie'
import BibliotecaFilmes from './BibliotecaFilmes'
import Filmes from './Filmes'
const App = () => {
  
  const[type,SetType] = useState(true) 
  const[infosSeries,setnfosSeries] = useState(null)
  const[infosMovie,setnfosMovie] = useState(null)
  const[exibir,SetExibir] = useState(false)
  
  const typeDefine = (props)=>{
    SetType(props)
  }
  const exibirFunction = (props)=>{
    SetExibir(props)
    console.log(props)
  }
  const RenovarArry = true
  useEffect(() => {
    //verificar se Arry de filmes base foi modificado
    let Biblioteca = BibliotecaSerie
    let Genre = GenresSeries
    let tipo = 'tv'
    //-----------
    
    if(localStorage.getItem("Arry-"+tipo) && RenovarArry===true){
      setnfosSeries(JSON.parse(localStorage.getItem("Arry-"+tipo)));
    }
    else {
      const buscar = async () =>{
        let busca = await Tmdb({Biblioteca, Genre,tipo})
        setnfosSeries(busca)
      }
      buscar()
    }
  }, []);
  useEffect(() => {
    //verificar se Arry de filmes base foi modificado
    let Biblioteca = BibliotecaFilmes
    let Genre = GenresFilmes
    let tipo = 'movie'
    //-----------
    if(localStorage.getItem("Arry-"+tipo) && RenovarArry===true){
      setnfosMovie(JSON.parse(localStorage.getItem("Arry-"+tipo)));
    }
    else {
      const buscar = async () =>{
        let busca = await Tmdb({Biblioteca, Genre,tipo})
        setnfosMovie(busca)
      }
      //buscar()
    }
  }, []);
  
  return (
    <div className="App">
    
    { 
      <Header infos={infosSeries} typeDefine={typeDefine} exibir={exibir}/>
    }
    {type && 
      <Series infos={infosSeries} exibir={exibir} 
      exibirFunction={exibirFunction} />
    }
    <Filmes infos={infosMovie} exibir={exibir} 
    exibirFunction={exibirFunction} />
    
    {!infosSeries && 
      <img src='https://www.hibrido.com.br/wp-content/uploads/2018/03/artigo2.gif' alt={'carregando'} style={{filter: 'invert(1)',objectFit: 'cover',width:'100%',height:'99.49vh',display: 'absolute',top:'0px',left:'0px'}}/>
    }
    
    </div>
    )
  }
  export default App;