import './Style/App.css';
import React from 'react';
import Cards from './Cards'
import Video from './Video'
import LastView from './LastView'
import { useState } from 'react';

const Series = ({infos,exibirFunction,exibir}) => {
  const[infoVideo,SetInfoVideo] = useState(false)
  const[infoEpsode,SetInfoEpsode] = useState(false) 

  
  
  const Play = (props) => {
    SetInfoVideo(props)
    exibirFunction(true)
    localStorage.setItem("LastView", JSON.stringify(props))
    numberEpisodes(props)
  }
  const numberEpisodes = (props)=> {
    let ArryEpsodes = props.numberEpisodes
    let LastUrls = []
    ArryEpsodes.forEach((x,i)=>{
      for(let z = 1; z <= x; z++){
        let b = ("/" + (i+1) + "/" + z + "/dub")
        LastUrls.push(b)
      }
    })
    SetInfoEpsode(LastUrls)
    console.log(props)
  }
  
  return (
    
    <div className="App" >
    
    {
      infos && exibir && 
      <Video infoVideo={infoVideo} infoEpsode={infoEpsode}/>
    }
    
    {
      infos && !exibir && 
      <LastView Play={Play} infos={infos}/>
    }
    
    {
      infos && !exibir && 
      infos.map((x,i)=>{return(infos[i].length>8 && <Cards key={i} Play={Play} Infos={x}/>)})
    }
    
    </div>
    )
  }
  export default Series;