import './Style/App.css';
import React from 'react';
import Cards from './Cards'
import Video from './Video'
import LastView from './LastView'
import { useState } from 'react';

const Filmes = ({infos,exibirFunction,exibir}) => {
  const[infoVideo,SetInfoVideo] = useState(false)

  const Play = (props) => {
    console.log('props: ',props)
    SetInfoVideo(props)
    exibirFunction(true)
    localStorage.setItem("LastView", JSON.stringify(props))
  }
  
  return (    
    <div className="App" >
    {infos && exibir && 
      <Video infoVideo={infoVideo} infoEpsode={''}/>
    }
    {infos && !exibir && 
      <LastView Play={Play} infos={infos}/>
    }
    <h1 style={{display: 'flex',margin:'10%'}}>Em produção...</h1>
    {infos && !exibir && 
      infos.map((x,i)=>{return(infos[i].length>1 && <Cards key={i} Play={Play} Infos={x}/>)})
    }    
    </div>
    )
  }
  export default Filmes;