import './Style/Video.css'
import { useState } from 'react';
const Video = ({infoVideo,infoEpsode}) => {
    //const urlbase = "https://embed.querofilmeshd.co/tv/";
    //const urlbase ="https://play.midiaflixhd.com/tv/"
    const urlbase = "https://player.uauflix.online/"
    const idUrl = infoVideo.url
    let localSauvo = 0
    if(localStorage.getItem(infoVideo.nome)){
        localSauvo = JSON.parse(localStorage.getItem(infoVideo.nome))
    }
    let urlTotal = urlbase+'tv/'+idUrl+infoEpsode[localSauvo]
    
    if(infoEpsode ===''){
        urlTotal = urlbase+idUrl
    }
    console.log(urlTotal)
    const[url,setUrl] = useState(urlTotal);
    
    
    let contar = 0
    
    const contador = (props)=>{
        let rever = JSON.parse(localStorage.getItem(infoVideo.nome))
        console.log('rever: ',rever)
        contar = rever+props;
        if(contar<0){contar=0}
        else if(contar>=infoEpsode.length){contar=infoEpsode.length-1}
        console.log('contar: ',contar)
        setUrl(urlbase+'tv/'+idUrl+infoEpsode[contar])
        localStorage.setItem(infoVideo.nome, JSON.stringify(contar))    
    }
    
    return(
        <>
        {infoEpsode !=='' ?
        <div className='Comands'>
        S{infoEpsode[localSauvo].slice(1,-4)}EP
        <button type='button' onClick={()=>{contador(-1)}}>Back</button>
        <button type='button' onClick={()=>{contador(1)}}>Next</button>
        </div>
        :
        <div></div>
    }
    <iframe title="Mflix" autoplay allowFullScreen scrolling="no" frameborder="0" src={url}></iframe>
    </>
    )
}
export default Video;