import React from 'react';
import './Style/Cards.css'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { useState } from 'react';

const Cards = ({Play, Infos}) =>{
    const [scrollX, setScrollX] = useState(-0)
    const UrlBase = 'https://image.tmdb.org/t/p/w300'
    const handleLeftArrow = (e) =>{
        let x = scrollX + Math.round(window.innerWidth/2);
        if(x>0){
            x = 0;
        }
        setScrollX(x)
    }
    const handleRightArrow = (e) =>{
        let x = 0
        let tamanhoMax = -e*150+window.innerWidth-60
        if( tamanhoMax < scrollX){ 
            if((scrollX - tamanhoMax) < window.innerWidth){
                x = tamanhoMax
            }
            else{
                x = scrollX - window.innerWidth*1/2
            }
        }
        else{
            x = tamanhoMax;
        }
        setScrollX(x)
    }
    //verificar se existe null no Arry
    let tamnhoList = []
    Infos.map(a=>{a.poster && tamnhoList.push(a.poster)})
    return(
        <>
        {Infos &&         
            <section  className={`movieRow`}>
            <h2>{Infos[0].genre}</h2>
            <div className='movieRow--left'  onClick={()=>{handleLeftArrow()}}>
            <ArrowBackIosIcon style={{fontSize:50}}/>
            </div>
            <div className='movieRow--right' onClick={()=>{handleRightArrow(Infos.length)}}>
            <ArrowForwardIosIcon style={{fontSize:50}}/>
            </div>
            <div className={`movieRow--listarea`} style={{
                overflowX: "hidden",
                transition: "all ease .5s",
                marginLeft: scrollX,
            }}>                  
            <div className='movieRow--list' style={{width: tamnhoList.length*150}}>
            {
                Infos.map((x,key)=>{
                    return(
                        x.poster &&
                        <div key={key} className='movieRow--item'>
                        <img src={UrlBase+x.poster} alt={x.nome}onClick={()=>{Play(x)}}/>
                        </div>
                        )
                    })
                }
                </div>
                </div>                        
                </section>
                
            }                    
            </>         
            )                     
        }
        export default Cards;