import React from 'react';
import './Style/LastView.css'
const LastView = ({infos, Play}) => {
    const urlbase = 'https://image.tmdb.org/t/p/original'
    let LastView = ''
    if(localStorage.getItem("LastView")){
        LastView = JSON.parse(localStorage.getItem("LastView"))
    }
    else{
        LastView = infos[4][0]
    }
    return(
        <section className='featured' style={{
            backgroundImage: `url(${urlbase+LastView.backdrop})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
        }}>
        <div className="featured--Vertical">
        <div className="featured--horizontal">
        <h1>{localStorage.getItem("LastView") && `Continue...`}<br></br></h1>
        <div className="featured--name"> {LastView.nome}</div>
        <div className="featured--info">
        <div className="featured--points">{LastView.rating} pontos</div>
        <div className="featured--year">{LastView.data && LastView.data.substring(0,4)}</div>
        <div className="featured--seasons">{LastView.numberEpisodes.length} temporada{LastView.numberEpisodes.length !== 1 ? 's':''}</div>
        </div>
        <div className="featured--description">{LastView.overview.length > 250 ? LastView.overview.substring(0,250)+'...':LastView.overview}</div> 
        <div className="featured--buttons">
        <button onClick={()=>{Play(LastView)}} value={LastView.url} className='featured--watch'>► Assistir</button>
        </div> 
        <div className="featured--generes">
        <strong>Gêneros: </strong>
        {LastView.genre}
        </div>
        </div>
        </div>        
        </section>
        )   
    }
    export default LastView;